<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item to="/home"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row>
        <el-col :span="12">
          <el-input placeholder="" class="input-with-select">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <el-table :data="orderList">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="order_number" label=""></el-table-column>
        <el-table-column prop="order_price" label="" width="100px"></el-table-column>
        <el-table-column prop="order_number" label="">
          <template slot-scope="scope">
            <el-tag
              :type="scope.row.order_pay == 0?'danger':'primary'"
            >{{scope.row.order_pay == 0 ?'':''}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label=""></el-table-column>
        <el-table-column label="" width="250px">
          <template slot-scope="scope">{{scope.row.create_time | dateFormat}}</template>
        </el-table-column>
        <el-table-column prop="order_number" label="">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="addDialogVisible=true"
            ></el-button>
            <el-button
              type="success"
              icon="el-icon-location"
              size="mini"
              @click="showAddressDialog"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%">
      <el-cascader v-model="selectedArea" :options="citydata" :props="{ expandTrigger: 'hover'}"></el-cascader>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addDialogVisible = false"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="addressDialogVisible" width="40%">
      <el-timeline>
        <el-timeline-item
          v-for="(item, index) in kuaidiList"
          :key="index"
          :timestamp="item.time"
        >{{item.context}}</el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>
<script>
// main.js
//
import citydata from "../../assets/citydata.js";
export default {
  created() {
    this.getOrderList();
  },
  data() {
    return {
      queryInfo: {
        pagenum: 1,
        pagesize: 5
      },
      total: 0,
      orderList: [],
      addDialogVisible: false,
      citydata,
      selectedArea: [],
      addressDialogVisible: false,
      kuaidiList: []
    };
  },
  methods: {
    async getOrderList() {
      const { data: res } = await this.$http.get("orders", {
        params: this.queryInfo
      });
      this.total = res.data.total;
      this.orderList = res.data.goods;
      console.log(this.orderList);
    },
    handleSizeChange(pagesize) {
      this.queryInfo.pagesize = pagesize;
      this.getOrderList();
    },
    handleCurrentChange(pagenum) {
      this.queryInfo.pagenum = pagenum;
      this.getOrderList();
    },
    async showAddressDialog() {
      const { data: res } = await this.$http.get("/kuaidi/1106975712662");
      this.kuaidiList = res.data;
      this.addressDialogVisible = true;
    }
  }
};
</script>
<style scoped lang="less">
</style>
